import React, { Component } from 'react';
import { Table } from 'antd';
import { setTableStatisCommon } from 'dump/data';

class TableStatisCommon extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Lớp/Nhóm',
        dataIndex: 'lopNhom',
        key: 'lopNhom',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'tenHocPhan',
        key: 'tenHocPhan',
      },
      {
        title: 'Số TC',
        dataIndex: 'soTC',
        key: 'soTC',
      },
      {
        title: 'Số SV đăng ký',
        dataIndex: 'svDangKy',
        key: 'svDangKy',
      },
      {
        title: 'Số SV tối thiểu',
        dataIndex: 'svToiThieu',
        key: 'svToiThieu',
      },
      {
        title: 'Số SV tối đa',
        dataIndex: 'svToiDa',
        key: 'svToiDa',
      },
      {
        title: 'Ca học',
        dataIndex: 'caHoc',
        key: 'caHoc',
      },
      {
        title: 'Lịch học',
        dataIndex: 'lichHoc',
        key: 'lichHoc',
      },
      {
        title: 'Phòng học',
        dataIndex: 'phongHoc',
        key: 'phongHoc',
      },
      {
        title: 'Giáo viên',
        dataIndex: 'giaoVien',
        key: 'giaoVien',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  render() {
    return (
      <Table
        bordered
        rowSelection={this.rowSelection}
        dataSource={setTableStatisCommon}
        columns={this.columns}
      />
    );
  }
}

export default TableStatisCommon;



