import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber, Radio } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime, timesRegister } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    const RadioGroup = Radio.Group;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Học kỳ:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm học:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Đợt:">
              {getFieldDecorator('timesRegister', {})(
                <Select>
                  {timesRegister.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <br />
        <Row gutter={24}>
          <Col span={24}>
            <Form.Item>
              {getFieldDecorator('conCerti', {})(
                <RadioGroup onChange={this.onChange} value={this.state.value}>
                  <Radio value={1}>Lớp đăng ký</Radio>
                  <Radio value={2}>Lớp đăng ký đủ</Radio>
                  <Radio value={3}>Lớp đăng ký thiếu</Radio>
                  <Radio value={4}>Lớp hủy</Radio>
                </RadioGroup>
              )}
            </Form.Item>
          </Col>
        </Row>
        <br />
      </Form>
    );
  }
}

export default FormInfoCommon;



