import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { CartHeadAction } from 'components/common';
import MenuTabTop from './MenuTabTop';
import FormInfoCommon from './FormInfoCommon';
import TableStatisCommon from './TableStatisCommon';
import './style.scss';

class StatisOfRegisNumbersPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Thống kê số lượng đăng ký">
        <CartHeadAction>
          <MenuTabTop />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableStatisCommon />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StatisOfRegisNumbersPage />
  </Page>
);



